* {
  font-family: sans-serif;
}
.App {
  display: grid;
  align-items: center;
  justify-items: center;
}
form {
  border: 2px solid rgb(37, 36, 36);
  padding: 4px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, .2);
}
.butt {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: 1em;
}
.result {
  border: 1px solid grey;
  background: whitesmoke;
  padding: .5em;
  border-radius: 5px;
}
input,button {
  font-size: 20px;
  padding: 10px;
  border-radius: 5px;
}
input {
  display: block;
  margin-bottom: 20px;

}
button {
  border: 1px solid grey;
  background: whitesmoke;
  margin-right: 5px;
}
button:nth-last-child(2),button:nth-last-child(1) {
  background: tomato;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
